import React from "react";
import { UseContext } from "../context/UseContext";
import "../styles/cartstyle.css";
import { ProductCart } from "./ProductCart";

const Cart = () => {
  const { modalCart, cart } = React.useContext(UseContext);
  return (
    <div className={modalCart ? "container-cart" : "container-cart-hide"}>
      <h1 className="mycart">Mi carrito de compras</h1>
      {cart.map((item) => {
        return <ProductCart item={item} />;
      })}

      <div className="flex-box">
        <p className="total-cart">Total: $30902029023</p>
        <div className="btn-confirmcart">Comprar</div>
      </div>
    </div>
  );
};

export { Cart };
