import React from "react";
import { UseContext } from "../context/UseContext";

import "../styles/product.css";

const Product = () => {
  const { product, setProduct, modal, setModal, cart, setCart, modalCart } =
    React.useContext(UseContext);
  const cerrarModal = () => {
    setModal(false);
  };

  const agregarCarrito = () => {
    setCart([
      ...cart,
      {
        title: product?.title,
        price: product?.price,
        image: product?.image,
      },
    ]);

    setModal(false);
  };

  return (
    <>
      <div
        className={
          modal || modalCart ? "background-black" : "background-black-hide"
        }
      ></div>
      <div className={modal ? "box-container" : "box-container-hide"}>
        <div
          className="btn-close"
          onClick={() => {
            cerrarModal();
          }}
        >
          X
        </div>
        <div className="img-product">
          <img src={product?.image} alt="product img" />
        </div>
        <div className="title-pro">{product?.title}</div>
        <div className="price-pro">${product?.price}</div>

        <div className="desc-pro">{product?.detail}</div>

        <div className="colors">
          <ul>
            <li>
              <label>
                <input type="radio" name="color" value="black" />
                <span
                  className="swatch"
                  style={{ backgroundColor: "#222" }}
                ></span>
              </label>
            </li>
            <li>
              <label>
                <input type="radio" name="color" value="blue" />
                <span
                  className="swatch"
                  style={{ backgroundColor: "#6e8cd5" }}
                ></span>
              </label>
            </li>
            <li>
              <label>
                <input type="radio" name="color" value="green" />
                <span
                  className="swatch"
                  style={{ backgroundColor: "#44c28d" }}
                ></span>
              </label>
            </li>
          </ul>
        </div>

        <div
          className="btn-product"
          onClick={() => {
            agregarCarrito();
          }}
        >
          Comprar
        </div>
      </div>
    </>
  );
};

export { Product };
