import React from "react";
import "../styles/bannerstyle.css";
import cartSVG from "../assets/shop-svgrepo-com.svg";
import logo from "../assets/logo-android-google-2-svgrepo-com.svg";
import { UseContext } from "../context/UseContext";

const Banner = () => {
  const { cart, modalCart, setModalCart } = React.useContext(UseContext);

  const openCart = () => {
    setModalCart(!modalCart);
  };

  return (
    <div className="container">
      <div className="banner">
        Nombre tienda
        <img
          src={logo}
          width="30px"
          height="100%"
          alt="logo"
          className="logo"
        />
      </div>
      <div
        className="shopping-cart"
        onClick={() => {
          openCart();
        }}
      >
        <img src={cartSVG} width="30px" height="100%" alt="carrito" />
        <div className="quantity">{cart.length}</div>
      </div>
    </div>
  );
};

export { Banner };
